import { useState, useEffect } from 'react'
import { useLocation } from "react-router-dom"
import { useUser } from "../../Context/Provider"
import { useSpring, useMotionValue } from 'framer-motion'

const useNavBar = () => {
    const [anchorEl, setAnchorEl] = useState(null)
    const [userMenuAnchorEl, setUserMenuAnchorEl] = useState(null)
    const [drawerSearchOpen, setDrawerSearchOpen] = useState(false)
    const [drawerOpen, setDrawerOpen] = useState(false)
    const [cartCount, setCartCount] = useState(3)
    const [notifyCount, setNotifyCount] = useState(3)
    const [favoritesCount, setFavoritesCount] = useState(3)
    const [drawerNotifyOpen, setDrawerNotifyOpen] = useState(false)
    const [total, setTotal] = useState(0)
    const [freeShippingProgress, setFreeShippingProgress] = useState(0)
    const [progressValue, setProgressValue] = useState(freeShippingProgress)
    const motionValue = useMotionValue(freeShippingProgress)
    const animatedValue = useSpring(motionValue, { stiffness: 100, damping: 20 })
    const FREE_SHIPPING_THRESHOLD = 100000
    const { token, cartItems } = useUser()

    const notifications = [
        { id: 1, title: 'Nueva actualización', description: 'Tu perfil ha sido actualizado correctamente.' },
        { id: 2, title: 'Promoción', description: '¡20% de descuento en todos los productos este fin de semana!' },
        { id: 3, title: 'Recordatorio', description: 'No olvides completar tu pedido pendiente.' },
    ]

    const toggleDrawer = open => event => {
        if (event.type === "keydown" && (event.key === "Tab" || event.key === "Shift")) return
        setDrawerOpen(open)
    }

    const toggleSearchDrawer = open => event => {
        if (event.type === "keydown" && (event.key === "Tab" || event.key === "Shift")) return
        setDrawerSearchOpen(open)
    }

    useEffect(() => {
        const newTotal = cartItems.reduce((sum, item) => sum + item.price * item.quantity, 0)
        setTotal(newTotal)
    }, [cartItems])

    useEffect(() => {
        const progress = Math.min((total / FREE_SHIPPING_THRESHOLD) * 100, 100)
        setFreeShippingProgress(progress)
    }, [total])

    useEffect(() => { 
        motionValue.set(freeShippingProgress)
    }, [freeShippingProgress, motionValue])
        
    useEffect(() => { 
        const unsubscribe = animatedValue.on("change", (latest) => { setProgressValue(latest) })
        return () => unsubscribe()
    }, [animatedValue, motionValue])

    const handleMenuOpen = event => setAnchorEl(event.currentTarget)
    const handleMenuClose = () => setAnchorEl(null)
    const handleUserMenuOpen = (event) => setUserMenuAnchorEl(event.currentTarget)
    const handleUserMenuClose = () => setUserMenuAnchorEl(null)
    const scrollToFooter = () => {
        window.scrollTo({
          top: document.documentElement.scrollHeight,
          behavior: "smooth"
        })
    }

    const isMenuOpen = Boolean(anchorEl)
    const isUserMenuOpen = Boolean(userMenuAnchorEl)
    const location = useLocation().pathname

    const state = {
        anchorEl,
        userMenuAnchorEl,
        cartCount,
        isMenuOpen,
        isUserMenuOpen,
        location,
        drawerOpen,
        token,
        favoritesCount,
        notifyCount,
        drawerNotifyOpen,
        notifications,
        drawerSearchOpen,
        total,
        progressValue,
        FREE_SHIPPING_THRESHOLD
    }
    
      const handlers = {
        handleMenuOpen,
        handleMenuClose,
        handleUserMenuOpen,
        handleUserMenuClose,
        setCartCount,
        toggleDrawer,
        setFavoritesCount,
        scrollToFooter,
        setNotifyCount,
        setDrawerNotifyOpen,
        toggleSearchDrawer
    }
    
    return { state, handlers }
}

export default useNavBar